// eslint-disable

import React from 'react';
import PT from 'prop-types';
import { Link, graphql } from 'gatsby';
import Image from "gatsby-image";

import Bio from '../components/bio';
import Layout from '../components/layout';
import SEO from '../components/seo';
import { CardBox } from '../components/cardbox';
import { MdTimer } from "react-icons/md";
import styles from "./index.module.scss";

const description = `
This is exactly how it looks when a programmer starts to write something else than a code. The
personal blog of one programmer.
`;

class BlogIndex extends React.PureComponent {
  render() {
    const { data, location } = this.props;
    const siteTitle = data.site.siteMetadata.title;
    const posts = data.allMarkdownRemark.edges;
    console.log(data);
    
    return (
      <Layout location={location} title={siteTitle} withVerticalPadding={false} withFooter>
        <SEO
          title="Main Page"
          description={description}
          keywords={['blog', 'javascript', 'react', 'personal', 'calculators']}
        />
        <CardBox>
          <Bio showWrittenBy={false} />
          {posts.map(({ node }) => {
            if (process.env.GATSBY_ENV === "production" && node.frontmatter.publish === false) {
              return null;
            }
            const title = node.frontmatter.title || node.fields.slug;
            
            return (
              <div className={styles.post}>
                <Link to={node.fields.slug}>
                  {
                    node.frontmatter.cover ? (
                      <div className={styles.coverWrapper}>
                        <Image
                          fluid={node.frontmatter.cover.childImageSharp.fluid}
                          alt="post cover"
                        />
                      </div>
                    ) : null
                  }
                  <h2 style={{ marginBottom: 4 }}>{title}</h2>
                  <div className={styles.metaInfo}>
                    <small>{node.frontmatter.date}</small>
                    <div>
                      <MdTimer size={12} />
                      <small>{node.timeToRead}m read</small>
                    </div>
                  </div>
                  <p dangerouslySetInnerHTML={{ __html: node.excerpt }} />
                </Link>
              </div>
            );
          })}
        </CardBox>
      </Layout>
    );
  }
}

BlogIndex.propTypes = {
  data: PT.objectOf(PT.any).isRequired,
  location: PT.objectOf(PT.any).isRequired,
};


export default BlogIndex;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          timeToRead
          excerpt
          fields {
            slug
          }
          frontmatter {
            cover {
              childImageSharp {
                fluid(traceSVG: {color: "#29276c", optCurve: true, threshold: 50}) {
                  ...GatsbyImageSharpFluid_tracedSVG
                }
              } 
            }
            date(formatString: "DD MMMM YYYY")
            title
            publish
          }
        }
      }
    }
  }
`;
